<template>
    <div class="p-20">

        <div class="filter-container" style="margin-bottom: 20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入KEY"
                      @keyup.native.enter="initData"
                      v-model="queryData.systemKey">
            </el-input>
            <el-button class="filter-item" @click="initData" size="small">查询</el-button>
            <el-button type="primary" @click="showAddParamDialog" v-if="permission.add" size="small">新增</el-button>
            <el-button type="primary" @click="clearParamsWarning" size="small">清空缓存</el-button>
        </div>

        <el-table
                ref="singleTable"
                :data="tableData"
                style="width: 100%">
            <el-table-column type="index" label="序号" width="110"></el-table-column>
            <el-table-column
                    v-for="item in tableHeader"
                    :key="item.prop"
                    :label="item.label"
                    :prop="item.prop"
                    :width="item.width"
                    :min-width="item.minWidth"
                    :align="item.align"
                    :formatter="item.formatter"
                    show-overflow-tooltip>
            </el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
                    width="80">
                <template scope="scope" class="filter-operation">
                    <ul style="list-style: none;padding: 0px">
                        <li style="float:left;width: 30px">
                            <el-button
                                    type="text" size="small"
                                    @click="getParam(scope.row.id)" v-if="permission.edit">修改
                            </el-button>
                        </li>
                    </ul>
                </template>
            </el-table-column>
        </el-table>

        <div class="pagination">
            <el-pagination @size-change="userPageSize" @current-change="userPage"
                           :current-page="queryData.pageNum"
                           :page-sizes="[10,20,30, 50]" :page-size="queryData.pageSize"
                           layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

        <el-dialog title="修改系统参数" :visible.sync="dialogVisible" @close="dialogClose" :close-on-click-modal="false">
            <el-form :model="paramsData" :rules="rules" ref="paramsData">
                <el-form-item :label-width="itemWidth" label="*KEY" prop="systemKey">
                    <el-input :disabled="disabled" class="input-short" v-model="paramsData.systemKey"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="选择图片">
                    <el-upload
                            class="upload-demo"
                            action="/upload/nofilter/file"
                            list-type="picture"
                            :on-success="handleSuccess"
                            :on-remove="handleRemove"
                            name="uploadFile"
                            :before-upload="handleBeforeUpload"
                            :autoUpload="true"
                            :data="uploadData"
                            :file-list="fileList">
                        <el-button size="small" type="primary">上传图片</el-button>
                    </el-upload>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*参数值" prop="systemValue">
                    <el-input class="input-short" v-model="paramsData.systemValue" type="textarea"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*备注" prop="description">
                    <el-input class="input-short" v-model="paramsData.description" type="textarea"
                              auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="validate">保 存</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>

    import fetch from '@/utils/fetch';

    export default {
        data() {
            let validate = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('不能为空'));
                }
                callback();
            };
            return {
                itemWidth: '160px',
                dialogVisible: false,
                paramsData: {
                    id: '',
                    systemKey: '',
                    systemValue: '',
                    description: ''
                },
                queryData: {
                    systemKey: '',
                    pageNum: 1,
                    pageSize: 10
                },
                rules: {
                    systemKey: [{validator: validate, trigger: 'blur'}],
                    systemValue: [{validator: validate, trigger: 'blur'}],
                    description: [{validator: validate, trigger: 'blur'}],
                },
                tableData: [],
                tableHeader: [{
                    label: "KEY",
                    prop: "systemKey",
                    width: 360
                }, {
                    label: "参数值",
                    prop: "systemValue",
                    width: 360
                }, {
                    label: "备注",
                    prop: "description"
                }],
                total: 0,
                uploadData: {
                    type: 1,
                    usageType: 3
                },
                fileList: [],
                action: '',
                disabled: true
            }
        },
        created() {
            this.initData();
        },
        methods: {
            handleRemove(file, fileList) {
                this.paramsData.systemValue = '';
            },
            handleSuccess(response, file, fileList) {
                this.fileList = [fileList[fileList.length - 1]];
                this.paramsData.systemValue = response.data;
            },
            handleBeforeUpload(file) {
                let filename = file.name.toLowerCase();
                if (!filename.endsWith('.jpg') && !filename.endsWith('.jpeg') && !filename.endsWith('.png')) {
                    this.$message.error('格式不对，请重新选择');
                    return false;
                }
                if (file.size > 1024 * 1024 * 2) {
                    this.$message.error('图片太大，请重新选择');
                    return false;
                }
                return true;
            },
            dialogClose() {
                this.resetData();
            },
            initData() {
                if (this.queryData.systemKey === '') {
                    this.getList();
                } else {
                    this.query();
                }
            },
            query() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: '/admin/systemparams/query',
                        method: 'get',
                        params: _this.queryData
                    }).then(function (response) {
                        if (response.result) {
                            _this.tableData = response.data;
                        }
                    }).catch();
                })
            },
            getList() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: '/admin/systemparamslist',
                        method: 'get',
                        params: _this.queryData
                    }).then(function (response) {
                        if (response.result) {
                            _this.tableData = response.data.list;
                            _this.total = response.data.page.count;
                        }
                    }).catch();
                })
            },
            getParam(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: '/admin/systemparams/' + id,
                        method: 'get'
                    }).then(function (response) {
                        if (response.result) {
                            _this.setData(response.data);
                            _this.dialogVisible = true;
                        }
                    }).catch();
                })
            },
            setData(data) {
                let key;
                for (key in this.paramsData) {
                    this.paramsData[key] = data[key] === null ? '' : data[key];
                }
            },
            resetData() {
                let key;
                this.$refs['paramsData'].resetFields();
                for (key in this.paramsData) {
                    this.paramsData[key] = '';
                }
                this.action = '';
                this.fileList = [];
                this.disabled = true;
            },
            modify() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/systemparams',
                        method: 'put',
                        data: _this.paramsData
                    }).then(function (response) {
                        if (response.result) {
                            _this.dialogVisible = false;
                            _this.initData();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            showAddParamDialog() {
                this.dialogVisible = true;
                this.action = 'add';
                this.disabled = false;
            },
            addParam() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/systemparams',
                        method: 'post',
                        data: _this.paramsData
                    }).then(function (response) {
                        if (response.result) {
                            _this.dialogVisible = false;
                            _this.initData();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            validate() {
                this.$refs['paramsData'].validate((valid) => {
                    if (valid) {
                        switch (this.action) {
                            case 'add':
                                this.addParam();
                                break;
                            default:
                                this.modify();
                                break;
                        }
                    }
                });
            },
            clearParams() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/systemparams/clear',
                        method: 'get'
                    }).then(function (response) {
                        if (response.result) {

                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            clearParamsWarning() {
                this.$confirm('确认清除缓存？', '提示', {
                    type: 'warning'
                }).then(() => {
                    this.clearParams();
                }).catch(() => {

                });
            },
            //翻页的按钮
            userPage(val) {
                this.queryData.pageNum = val;
                this.initData();
            },
            //获取每页多少条
            userPageSize(val) {
                this.queryData.pageSize = val;
                this.initData();
            },
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .input-short {
        width: 300px;
    }
</style>



